@import "./../../assets/styles/mixins";
@import "./../../assets/styles/variables";

.contacts {
  flex: 1 1 100%;
  background: $lightGray;
  @include displayFlex(row, center);
  align-items: center;
  & h2{
    text-align: left;
    font-weight: 800;
    margin-bottom: 62px;
  }
}

.wrapper {
  flex: 0 1 880px;
  flex-direction: column;
  align-items: center;
}

.map{
  width: 100%;
  height: 400px;
  margin-bottom: 50px;
  & iframe{
    width: inherit;
    height: inherit;
    border:0;
    -webkit-filter: grayscale(99%); /* Chrome 19+ & Safari 6+ */
    -webkit-backface-visibility: hidden;  /* Fix for transition flickering */
  }
}

.form {
  @include displayFlex(column, center);
  gap: 20px;
  font-size: 1.25rem;
  margin-bottom: 30px;
  @media screen {
    @media (max-width: $maxWidthBreak992) {
      width: 480px;
    }
    @media (max-width: $maxWidthBreak768) {
      width: 90%;
    }
  }

  & input {
    height: 30px;
    border: 1px solid rgba(0,0,0,0.2);
    padding: 5px 15px;
    font-style: italic;
    background: $colorWhite;

    &::placeholder {
      font-style: italic;
      font-weight: bold;
      color: #7c7c7c;
    }

    &:focus {
      outline: none;
      border-color: $colorBlack;
    }
  }

  & textarea {
    min-height: 100px;
    resize: none;
    font-style: italic;
    font-weight: bold;
    border: 1px solid rgba(0,0,0,0.2);
    color: #7c7c7c;
    padding: 15px;
    background: $colorWhite;

    &::placeholder {
      font-weight: bold;
      color: $placeHolderColor;
    }

    &:focus {
      outline: none;
      border-color: $colorBlack;
    }
  }

}