//breaks
$maxWidthBreak1080: 1080px;
$maxWidthBreak992: 992px;
$maxWidthBreak768: 768px;
$maxWidthBreak480: 480px;
//fonts
$montserratFont: "Montserrat", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
$robotoFont: "Roboto Slab", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
//colors
$placeHolderColor: #7c7c7c;
$focusColor: #ffc8008a;
$inputWhite: #f8f9fa;
$borderGray: #c0c0c06e;
$lightGray: #f8f8f8;
$colorGray: #767676;
$backgroundLightGray: #e9ecef;
$colorBlack: #18191a;
$lightBlack: #6c757d;
$textSemiWhite: rgba(255, 255, 255, 0.8);
$colorWhite: rgba(255, 255, 255, 1);

h1 {
  font-size: 3.4375rem;
  font-weight: 800;
  line-height: 1.2;
  letter-spacing: .5px;
  margin: 0 0 19px 0;
  word-wrap: break-word;
  text-transform: uppercase;
  color: $colorBlack;
  @media screen {
    @media (max-width: 992px) {
      font-size: 2rem;
      letter-spacing: 0;
    }
    @media (max-width: 480px) {
      font-size: 1.5rem;

    }
  }
}

h2 {
  font-size: 1.875rem;
  font-weight: 800;
  text-transform: capitalize;
}

h3 {
  font-size: 1.25rem;
  text-align: center;
  font-family: $montserratFont;
  font-weight: 700;
  line-height: 1.2;
  letter-spacing: 1px;
}

h4 {
  font-size: 1.125rem;
  text-align: center;
  margin: 15px 0;
}

button {
  font-size: 15px;
  line-height: 15px;
  padding: 19px 40px;
  border: none;
  cursor: pointer;
  background-color: $colorBlack;
  color: $colorWhite;
  transition: all .3s ease;

  &:hover {
    letter-spacing: 2px;
  }
}
