@import './../../assets/styles/variables';
@import "./../../assets/styles/mixins";

.About{
  height: 100vh;
  flex: 1 1 100%;
  justify-content: center;
  align-items: center;
  background-color: $lightGray;
  display: flex;
  flex-direction: column;

  .wrapper{
    @include displayFlex(column, center);
    width: 750px;
    & h3{
      font-size: 1.375rem;
      font-weight: 700;
      letter-spacing: 0.5px;
      align-self: flex-start;
      margin: 48px 0 23px 0;
    }
  }



  & .textWrapper{
    @include displayFlex(row, space-between);
    gap: 50px;
    & .descriptionText{
      width: 45%;
      line-height: 30px;
      font-style: italic;
      & p{
        margin-bottom: 35px;
      }
    }

    & .aboutTextWrapper{
      width: 45%;
      & .aboutText{
        display: grid;
        grid-template-columns: 1fr 2fr;
        &Title{
          font-weight: 700;
        }
      }

    }
    & p{
      color: $colorGray;
    }
  }
}

