@import "./assets/styles/mixins";

.App{
  @include displayFlex(row, flex-start);
  flex: 1 1 100%;
}

.toTop {
  position: fixed;
  bottom: 35px;
  right: 50px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  @include displayFlex(row, center);
  align-items: center;
  background-color: rgba(231, 231, 231, 0.9);
  box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.6);
  @media screen {
    @media (max-width: 480px) {
      bottom: 15px;
      right: 15px;
    }
  }
}

.hidden {
  display: none;
}